import axios from "axios"
import { TOAST_ERROR, TOAST_SUCCESS } from "."
import { ADD_FEED_ACCOUNT } from "../actions/addFeedActions"
import { BASE_URL, CALLBACK_URI, HOST_URL, IS_API_ACCESS_LIVE, REDIRECT_URI } from "../actions/api"
import store from "../store"
import { TAGGBOX_USER_HOME_DATA } from "../actions/sceneActions"

const channel = new BroadcastChannel('connect');

export const addTwitterAccount = (code, networkId, navigation) => {
    const refresh = JSON.parse(localStorage.getItem("refresh"))
    const feedId = JSON.parse(localStorage.getItem('feedId'))
    var accObj = {
        networkId: networkId,
        grant_type: "authorization_code",
        code_verifier: "challenge",
        created: 1675254640,
        client_id: IS_API_ACCESS_LIVE ? "QWlMNkZFSEQ4Tkl3WDZHLUJ2Ung6MTpjaQ" : 'NGxwallNUzFOM0xtTFB4UER5dlg6MTpjaQ',
        redirect_uri: 'https://test.taggbox.com/taggshop/feeds/authCallback/',
        code,
        // is_v2: 1
        // networkId: networkId,
        // validity: 1704525174,
        // oauth_token: oauth_token,
        // oauth_verifier: oauth_verifier,
        // tokenSecret: tokenSecret,
        // wallId: localStorage.getItem('wallId')
    }
    if (refresh) accObj['reconnectAccount'] = 1
    else accObj['reconnectAccount'] = 0
    if (feedId) accObj['feedId'] = feedId
    const url = localStorage.getItem("url_redirect")
    ADD_FEED_ACCOUNT(accObj).then(res => {
        const { responseCode, resposneMessage, responseData } = res.data
        if (responseCode == 200) {
           try {
             store.dispatch(TAGGBOX_USER_HOME_DATA())
           } catch (error) {
            console.log(error)
           }
            TOAST_SUCCESS(resposneMessage)
        }
        else TOAST_ERROR(resposneMessage)
        localStorage.removeItem('feedId')
        setTimeout(() => {
            // window.location = url
            navigation(url)
        }, 1000);
    }).catch(err => {
        localStorage.removeItem('feedId') 
        TOAST_ERROR('Some Error Occured!')
        setTimeout(() => {
            // window.location = url
            navigation(url)
        }, 1000);
    })
}

export const addInstaPersonal = (navigation, code) => {
    alert("addInstaPersonal")
    const url_redirect = localStorage.getItem("url_redirect")
    const url = `https://cors.bridged.cc/https://api.instagram.com/oauth/access_token`;
    const feedId = JSON.parse(localStorage.getItem('feedId'))
    const refresh = JSON.parse(localStorage.getItem("refresh"))
    var data = new FormData();
    data.append('client_id', '480810795949098');
    data.append('client_secret', '48c75d71ec8faf89c59b6f4d69bccb89');
    data.append('grant_type', 'authorization_code');
    data.append('redirect_uri', `${BASE_URL}/feeds/authCallback/`);
    data.append('code', code);
    axios.post(url, data, { headers: { "Content-Type": "application/x-www-form-urlencoded", "x-cors-grida-api-key": "875c0462-6309-4ddf-9889-5227b1acc82c" } }).then(res => {
        const { access_token } = res.data

        var accObj = {
            token: access_token || "",
            networkId: 2,
            validity: 34656,
            wallId: localStorage.getItem('wallId')
        }
        if (feedId) accObj['feedId'] = feedId
        if (refresh) accObj['reconnectAccount'] = 1
        else accObj['reconnectAccount'] = 0
        try {
            ADD_FEED_ACCOUNT(accObj).then(res => { 
                const { responseCode, resposneMessage, responseData, createdAccount } = res.data 
                // if (responseCode == 200) {
                //     TOAST_SUCCESS(resposneMessage)
                //     // store.dispatch(TAGGBOX_USER_HOME_DATA())

                // }
                // else TOAST_ERROR(resposneMessage)
                localStorage.removeItem('feedId')
                window.opener.postMessage({error:responseCode !== 200, message: resposneMessage}, '*');
                window.close()
                setTimeout(() => {
                    // navigation(url_redirect)
                    // window.location = url_redirect
                }, 1000);
            })
        } catch (error) {
            // TOAST_ERROR('Some Error Occured!')
            localStorage.removeItem('feedId') 
            window.opener.postMessage({error: true, message: 'Some Error Occured!'}, '*');
            window.close()
            setTimeout(() => {
                // navigation(url_redirect)
                // window.location = url_redirect
            }, 1000);
        }

    })
}

export const AddFbAccount = (c, navigation) => {
    alert("AddFbAccount")
    const feedId = JSON.parse(localStorage.getItem('feedId'))
    const refresh = JSON.parse(localStorage.getItem("refresh"))
    const ugcAccAdd = JSON.parse(localStorage.getItem("ugcAccAdd")) 
    const url_redirect = localStorage.getItem("url_redirect")
    const networkId = parseInt(localStorage.getItem("networkId"))
    axios.get(`https://graph.facebook.com/v15.0/oauth/access_token?client_id=${'117029975354072'}&redirect_uri=${`${BASE_URL}/feeds/authCallback/`}&client_secret=${"0cd429359e4892f79065f9810d5c906c"}&code=${c}`).then(res => {
        const { data } = res 
        if (data) {

            var accObj = {
                token: data.access_token || "",
                networkId: networkId,
                validity: data.expires_in || 34656,
                reconnectAccount: 0,
                wallId: localStorage.getItem('wallId')
            }
            if (feedId) accObj['feedId'] = feedId
            if (refresh) accObj['reconnectAccount'] = 1
            ADD_FEED_ACCOUNT(accObj).then(res => {
                const { responseCode, resposneMessage, responseData } = res.data
                // if (responseCode == 200) {
                //     TOAST_SUCCESS(resposneMessage)
                //     // store.dispatch(TAGGBOX_USER_HOME_DATA())

                // }
                // else TOAST_ERROR(resposneMessage)
                localStorage.removeItem('feedId')
                // setTimeout(() => {
                    // alert(url_redirect)
                    // window.location = url_redirect
                    if(ugcAccAdd) {
                        localStorage.removeItem("ugcAccAdd")
                        localStorage.removeItem("addAcc")
                        window.close()
                    }
                    // else {
                        // navigation(url_redirect)
                        window.opener.postMessage({error:responseCode !== 200, message: resposneMessage}, '*');
                        window.close()
                    // }
                // }, 500);
            })
        }
    }).catch(error => {
        localStorage.removeItem('feedId')
        setTimeout(() => {
            // window.location = url_redirect
            // navigation(url_redirect)
            window.close()
        }, 1000);
    })
}

