import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { BASE_URL, CALLBACK_URI, CLOUDPATH_TAGGSHOP } from '../../../actions/api';
import { Link, useNavigate, useSearchParams } from 'react-router-dom';
import Logo from '../sideMenuBar/logo';
import { AddFbAccount, addGoogleAccount, addInstaPersonal, addTwitterAccount, addVimeoAccount } from '../../../utils/reconnectAccount';
import { CHECK_USER_ACCOUNT } from '../../../actions/ugcAction';
import { googleUserDetails } from '../../../utils/auth';

const socialLogin = new BroadcastChannel('socialLogin');

const UgcCallback = () => {
    const [searchParams, setSearchParams] = useSearchParams();
    const [onError, setOnError] = useState(false)
    const navigation = useNavigate()

    const [code, setCode] = useState(searchParams.get("code"))

    useEffect(() => {
        const channel = new BroadcastChannel('connect');
        const url = `https://cors.bridged.cc/https://api.instagram.com/oauth/access_token`;
        const networkId = JSON.parse(localStorage.getItem('networkId'))
        if (networkId == 2) {
            if (JSON.parse(localStorage.getItem('shareAccount'))) addInstaPersonal(navigation, code)
            else {
                var data = new FormData();
                data.append('client_id', '480810795949098');
                data.append('client_secret', '48c75d71ec8faf89c59b6f4d69bccb89');
                data.append('grant_type', 'authorization_code');
                data.append('redirect_uri', `${BASE_URL}/feeds/authCallback/`);
                data.append('code', code);
                axios.post(url, data, { headers: { "Content-Type": "application/x-www-form-urlencoded", "x-cors-grida-api-key": "875c0462-6309-4ddf-9889-5227b1acc82c" } }).then(res => {
                    const { access_token } = res.data

                    localStorage.setItem('ugcAuth', false)
                    // localStorage.setItem('ugcObj', JSON.stringify(res.data))
                    const reqUrl = `https://graph.instagram.com/me?fields=username&access_token=${access_token}`
                    axios.get(reqUrl).then(res => {
                        localStorage.setItem('instagram_ugc', JSON.stringify(res.data))
                        if (String(res.data.username) == String(localStorage.getItem('ugc_account_check'))) {
                            window.close()
                        }
                        else setOnError(true)
                    }).catch(err => {
                        window.close()
                    })

                })
            }
        }
        if (networkId == 3) {
            if (JSON.parse(localStorage.getItem('shareAccount'))) {
                // alert('here!')
                AddFbAccount(searchParams.get('code'), navigation)
            }
            else {
                axios.get(`https://graph.facebook.com/v15.0/oauth/access_token?client_id=${'117029975354072'}&redirect_uri=${`${BASE_URL}/feeds/authCallback/`}&client_secret=${"0cd429359e4892f79065f9810d5c906c"}&code=${code}`).then(res => {
                    const { access_token } = res.data
                    axios.get(`https://graph.facebook.com/v15.0/me?access_token=${access_token}`).then(res => {
                        localStorage.setItem('instagram_ugc', JSON.stringify(res.data))
                        const userId = localStorage.getItem('userId')
                        if (parseInt(JSON.parse(userId)) == parseInt(res.data.id)) window.close()
                        else setOnError(true)

                        localStorage.removeItem('ugcAuth')
                        // window.close()
                    })
                }).catch(error => {
                    // window.location = `${HOST_URL}moderation/index/${feed_obj.wallId}`
                })
            }
        }
        if (networkId == 1) {
            if (JSON.parse(localStorage.getItem('shareAccount'))) {
                // alert('here!')
                addTwitterAccount(code, networkId, navigation)
            }
            else {
                const accObj = {
                    networkId: 1,
                    client_id: "Z09GV2tkZ0g2alJQckpSUkd3bEc6MTpjaQ",
                    redirect_uri: 'https://test.taggshop.io/feeds/authCallback/',
                    code,
                    token: 'n',
                    // validity: 1704525174,
                    // oauth_token: oauth_token,
                    // oauth_verifier: oauth_verifier,
                    // tokenSecret: tokenSecret,
                    reconnectAccount: 0,
                    // wallId: window.wallID
                }
                CHECK_USER_ACCOUNT(accObj).then(res => {
                    const { responseData } = res.data
                    const obj = { username: responseData.userName, profile: responseData.profile_image_url, id: responseData.userId }
                    localStorage.setItem('instagram_ugc', JSON.stringify(obj))
                    localStorage.removeItem('ugcAuth')
                    channel.postMessage(JSON.stringify(obj));
                    if (String(res.data.userName) == String(localStorage.getItem('ugc_account_check'))) {
                        window.close()
                    }
                    else setOnError(true)
                })
            }
        }
        if (networkId == 8) {
            if (JSON.parse(localStorage.getItem('shareAccount'))) {
                // alert('here!')
                addVimeoAccount(searchParams.get('code'), navigation)
            }
            else {
                const accObj = {
                    networkId: 8,
                    wallId: localStorage.getItem('wallId'),
                    code,
                    token: 'n',
                    // validity: 1704525174,
                    // oauth_token: oauth_token,
                    // oauth_verifier: oauth_verifier,
                    // tokenSecret: tokenSecret,
                    reconnectAccount: 0,
                    // wallId: window.wallID
                }
                CHECK_USER_ACCOUNT(accObj).then(res => {
                    const { responseData } = res.data
                    const userId = localStorage.getItem('userId')
                    const obj = { username: responseData.name, profile: responseData.picture, id: responseData.id }
                    localStorage.setItem('instagram_ugc', JSON.stringify(obj))
                    localStorage.removeItem('ugcAuth')
                    if (parseInt(JSON.parse(userId)) == parseInt(res.data.id)) window.close()
                    else setOnError(true)
                })
            }
        }
        if (networkId == 7) {
            var payload = {
                code,
                client_id: "466192424702-c542t25gv7ju9bjvpee935oo9b1enscm.apps.googleusercontent.com",
                client_secret: "c0ZSs68M4CL6_emnsP0WekeH",
                redirect_uri: `${BASE_URL}/feeds/authCallback`,
                access_type: "offline",
                grant_type: "authorization_code",
            }

            axios.post(`https://oauth2.googleapis.com/token`, payload).then(res => {
                axios.get(`https://www.googleapis.com/oauth2/v3/userinfo?access_token=${res.data.access_token}`).then(res => {
                    const obj = { username: res.data.name, profile: res.data.picture, id: res.data.sub, email: res.data.email }
                    localStorage.setItem('instagram_ugc', JSON.stringify(obj))
                    channel.postMessage(JSON.stringify(obj));
                    if (String(res.data.name) == String(localStorage.getItem('ugc_account_check'))) window.close()
                    else setOnError(true)
                })
            })
        }
        if (networkId == 18) {
            if (JSON.parse(localStorage.getItem('shareAccount'))) AddFbAccount(searchParams.get('code'), navigation)
        }
        if (networkId == 4) {
            if (JSON.parse(localStorage.getItem('shareAccount'))) {
                addGoogleAccount(searchParams.get('code'), navigation, () => { })
            }
            if (JSON.parse(localStorage.getItem("socialLogin"))) {
                const payload = {
                    code: code,
                    client_id: "466192424702-c542t25gv7ju9bjvpee935oo9b1enscm.apps.googleusercontent.com",
                    client_secret: "c0ZSs68M4CL6_emnsP0WekeH",
                    redirect_uri: `${BASE_URL}/feeds/authCallback`,
                    access_type: "offline",
                    grant_type: "authorization_code"
                }

                axios.post(`https://oauth2.googleapis.com/token`, payload).then(res => {
                    googleUserDetails(res.data.access_token).then(res => {
                        
                        try {
                            const { data } = res
                            var picture = data.picture ? data.picture : "";
                            const obj = { accountFullName: data.name, accountEmailId: data.email, createFrom: 'google', picture , authId: data.sub }
                            socialLogin.postMessage(JSON.stringify(obj));
                            setTimeout(() => {
                                window.top.close();
                            }, 2000)
                        } catch (error) {
                          
                        }
                    })
                    // if (data && Object.keys(data).length > 0) {
                    //     var picture = data.picture ? data.picture : "";
                    //     const {utm_campaign,utm_source,firstInteractionPage} = this.state;

                    // }
                })
            }
        }
    }, [code])

    const onSwitchAccount = () => {
        const networkId = JSON.parse(localStorage.getItem('networkId'))
        switch (networkId) {
            case 2:
                return "https://www.instagram.com/"
            case 1:
                return "https://www.twitter.com/"
            case 3:
                return "https://www.facebook.com/"
            case 7:
                return "https://www.google.com/"
            case 8:
                return "https://www.vimeo.com/"
            default:
                break;
        }
    }

    return (
        onError ? <>
            <div className="page-header navbar">

                <div className="page-header-inner ">

                    <Logo />
                </div>
            </div>

            <div className="page-container">

                <div className="page-content-wrapper">
                    <div id="controllerContent">
                        <div className="controller" id="feeds">
                            <div className="action" id="feedsauthCallback">
                                <div className="container ugc-righterror-container">
                                    <div className="card card-body py-5 ugc-righterror">
                                        <div className="ugc-reinn text-center my-8">
                                            <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/products/product-empty.svg`} alt='account_error' className="img-fluid mx-auto mb-5" style={{ maxWidth: "200px" }} />
                                            <h1 className="mb-4">Oh Snap! You have chosen the wrong account.</h1>
                                            <p>Connect using <b>@{localStorage.getItem('ugc_account_check')}</b></p>
                                            <div className="d-flex flex-column justify-conent-center w-auto mx-auto" style={{ maxWidth: "180px" }}>
                                                <a className="btn btn-danger change-account mb-5" href={onSwitchAccount()}>Switch Account</a>
                                                <Link to={`/ugc_right/${localStorage.getItem('unique_code')}`} className="go-back d-flex align-items-center justify-content-center w-auto" style={{ cursor: "pointer" }}><i className="fa fa-arrow-left-alt text-link"></i> Go Back</Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div> </div>
                </div>

            </div>
        </> : <></>
    )
}

export default UgcCallback