import React, { Component } from 'react'
import { connect, useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { getUserInfo_afterChanges } from '../../../actions/loginActions'
import { PLAN__DETAILS, UPDATE_EC_FREE_PLAN } from '../../../actions/planDetail'
import { togglePriceSection } from '../../../actions/show_modal_Action'
import { TOAST_SUCCESS } from '../../../utils'


const PlanExpire=(props)=>{
      const navigation = useNavigate()
       const dispatch = useDispatch()
    return <Plan_Expire navigation={navigation} dispatch={dispatch}/>
}
 
 class Plan_Expire extends Component {

    updatePlan=(planId)=>{
        const { navigation, dispatch } = this.props
        UPDATE_EC_FREE_PLAN(planId).then((response) => {
          const { response_code , response_message} = response.data
          if(response_code==200){
              TOAST_SUCCESS(response_message);
              dispatch(getUserInfo_afterChanges())
              dispatch(PLAN__DETAILS()) 
            navigation('/walls/home') 
          } 
      })
      }

  render() {

    return (
        <div className="d-flex flex-column flex-root" style={{minHeight: 'calc(100vh - 65px)'}}>
            <div className="d-flex flex-row-fluid">
                <div className="small_container d-flex flex-row-fluid flex-column justify-content-center px-4 text-center">
                    <div className="upgrade_succ_wrap p-10 position-relative d-flex align-items-center justify-content-center">
                        <img src="https://cloud.taggbox.com/taggshop/app/assets/media/success/Upgrade-Account.svg" className="img-fluid position-relative" alt="" height={160} width={164} />
                    </div>
                    <h2 className="font-weight-boldest mb-8">Woopsie! Your Plan Has Expired</h2>
                    <p className="font-size-h6">We have just paused your account access for now. But no need to worry, It will resume once you upgrade your plan</p>
                    <p className="font-size-h6">Are you still undecided about upgrading? Then you are welcome to extend your free trial or book a quick demo with us</p>
                    <div className="d-flex align-items-center flex-wrap justify-content-center my-10">
                        <a className="btnUpgrade btn btn-primary btn-lg btn-min-w mx-3 mb-5" onClick={()=>{this.props.dispatch(togglePriceSection(true, 2))}}>Upgrade Now!</a>
                        <a className="popUp extendPlanBtn btn btn-outline-primary btn-lg btn-min-w mx-3 mb-5" onClick={()=>this.updatePlan(57)} >Switch To Forever Free Plan</a>
                        <a href="https://taggshop.io/book-demo/" target="_blank" className="btnUpgrade btn btn-primary btn-lg btn-min-w mx-3 mb-5">Book a Demo</a>
                    </div>
                    <div className="d-flex align-items-center flex-wrap justify-content-center mb-6">
                    </div>
                </div>
            </div>
        </div>
    )
  }
}

 
  
  export default  (PlanExpire);