import { useEffect } from "react"
import { useNavigate, useParams } from "react-router-dom";
import LoadingScreen from "../pages/LoadingScreen";
import HttpClient from "../../actions/http-client";
import { COLLABORATOR_REJECT, HOST_URL } from "../../actions/api";
import axios from "axios";


function RejectCollabratorRequest(){
    const params = useParams();
    const navigate = useNavigate();
    useEffect(()=>{
        const ownerId = params.userId;
        const wallId = params.wallId;
        axios.delete(`${COLLABORATOR_REJECT}/${ownerId}/${wallId}`).then((response)=>{
             const {response_code}=response.data;
             window.location.href= HOST_URL;
            //  if(response_code===200){
            //    if(JSON.parse(localStorage.getItem('token'))){
            //        navigate(`/${getInitialRoute()}/walls/home`);
            //    }else{
                 window.location.href="https://taggbox.com/invitation-reject/";
            //    }
            //  }
        }).catch(ex=>{
            localStorage.clear();
            window.location.href="https://taggbox.com/invitation-reject/";
        });
    },[])
    return <LoadingScreen/>
}

export default RejectCollabratorRequest;