import { Helmet, HelmetProvider } from "react-helmet-async";


const PageHead = () => {
    return <>
        <HelmetProvider>
            <Helmet>
                <title>{
                          String(window.location.href).includes("/accounts/login") ? `Login | Tagshop`
                        : String(window.location.href).includes("/accounts/register") ? `Sign up | Tagshop` 
                        : String(window.location.href).includes("/accounts/verify") ? `Verify account | Tagshop` 
                        : String(window.location.href).includes("/accounts/otherinfo") ? `Other Info | Tagshop` 
                        : String(window.location.href).includes("/accounts/plan") ? `Select plan | Tagshop`  
                        : String(window.location.href).includes("/accounts/forgotpassword") ? `Forgot Password | Tagshop`  
                        : String(window.location.href).includes("/accounts/resetpassword") ? `Reset Password | Tagshop`  
                        : String(window.location.href).includes("/accounts/otherinfo") ? `Other Info | Tagshop`  
                        : String(window.location.href).includes("/accounts/verifyemail") ? `Verify Email | Tagshop`  
                        : String(window.location.href).includes("/accounts/verify") ? `Verify | Tagshop`  
                        : String(window.location.href).includes("/walls/home") ? `Dashboard | Tagshop` 
                        : String(window.location.href).includes("/content/moderation") ? `Moderation | Tagshop` 
                        : String(window.location.href).includes("/content/addfeed") ? `Add Feed | Tagshop` 
                        : String(window.location.href).includes("/content/managefeed") ? `Manage feed | Tagshop` 
                        : String(window.location.href).includes("/content/products") ? `Products | Tagshop` 
                        : String(window.location.href).includes("/content/apps&more") ? `Apps & More | Tagshop` 
                        : String(window.location.href).includes("/website") ? `Website | Tagshop` 
                        : String(window.location.href).includes("/emailCampaign") ? `Email Campaign | Tagshop` 
                        : String(window.location.href).includes("/shoponBio") ? `Shop On Bio | Tagshop` 
                        : String(window.location.href).includes("/influencer") ? `Creator | Tagshop` 
                        : String(window.location.href).includes("/transactions") ? `My Account - Tagshop`  
                        : String(window.location.href).includes("analytic") ? `Analytics | Tagshop`
                        : String(window.location.href).includes("/profile") ? `Profile | Tagshop`
                        : String(window.location.href).includes("/notifications") ? `Notifications | Tagshop`
                        : String(window.location.href).includes("preview") ? `Walls - Tagshop` : null

                }</title>
            </Helmet>
        </HelmetProvider>

    </>
}

export default PageHead