// import Login from '../components/loginSignUp/login'
// import SignUp from '../components/loginSignUp/signUp'
// import ForgotPassword from '../components/loginSignUp/forgotPassword'
// import ResetPassword from '../components/loginSignUp/resetPassword'
import { BrowserRouter, Routes, Route, Navigate, useParams, useNavigate, useLocation } from "react-router-dom";

// import OtherInfo from '../components/loginSignUp/otherinfo'
// import VerifyEmail from '../components/loginSignUp/verifyEmail'
// import AccountVerify from '../components/loginSignUp/accountVerify'
// import GettingStarted from "../components/GettingStarted/GettingStarted";
// import NewUserPricePage from '../components/plans/newUserPricePage'

import { LoginLayout } from "../components/loginSignUp/LoginLayout";
import ErrorFound from "../ErrorFound";
import planExpire from "../components/plans/planExpire";
import { LinkedInCallback } from "react-linkedin-login-oauth2";
import { HOST_URL } from "../../actions/api";
import { getInitialRoute } from "../../utils";
import { CollabratorConfirmAccount } from "./CollabratorConfirmAccount";
import RejectCollabratorRequest from "./RejectCollabratorRequest";
import UgcCallback from "../components/UGC_Request/UgcCallback";
import UgcScreen from "../components/UGC_Request/UgcScreen";
import { BIGCOMMERCE_LOAD_LOGIN, installShopify, loginWithBigcommerce, loginWithShopify } from "../../actions/loginActions";
import ShareAccount from "../components/shareAccount";
import LoadingScreen from "../pages/LoadingScreen";
import PageHead from "./head";
import UgcFallback from "../components/UGC_Request/UgcFallback";
import Authorization from "../../utils/Authorization";
// import ShopifyLoadingScreen from '../components/shopifyLoadingScreen'
 

const Login = lazy(() => import('../components/loginSignUp/login'));
const SignUp = lazy(() => import('../components/loginSignUp/signUp'));
const ForgotPassword = lazy(() => import('../components/loginSignUp/forgotPassword'));
const ResetPassword = lazy(() => import('../components/loginSignUp/resetPassword')); 

const OtherInfo = lazy(() => import('./../components/loginSignUp/otherinfo'));
const VerifyEmail = lazy(() => import('../components/loginSignUp/verifyEmail'));
const AccountVerify = lazy(() => import('../components/loginSignUp/accountVerify'));
const GettingStarted = lazy(() => import('../components/GettingStarted/GettingStarted'));
const NewUserPricePage = lazy(() => import('../components/plans/newUserPricePage')); 
const ShopifyLoadingScreen = lazy(() => import('../components/shopifyLoadingScreen')); 

const UpdateResetPassword = (props) => {
  const params = useParams();
  return <Suspense fallback={null}><ResetPassword prefix={props.prefix} params={params} /></Suspense>
}

const UpdateVerifyEmail = (props) => {
  const params = useParams();
  return <Suspense fallback={null}><VerifyEmail prefix={props.prefix} params={params} history={props.history} /></Suspense>
}

const SwitchInTaggshop = (props) => {
  const location = useLocation();
  const token = new URLSearchParams(location.search).get("token");
  const callback = new URLSearchParams(location.search).get("callback");
  console.log("token", token)
  console.log("callback", callback)
  if (token) {
    localStorage.setItem("token", token)
    window.location.href = callback
  }
  return <LoadingScreen />
}

const Redirectlogin = (props) => {
  localStorage.clear()
  const params = useParams();
  const isAdmin = window.location.href.includes('admin')
  if(isAdmin) localStorage.setItem("isAdmin", 1)
  else localStorage.setItem("isAdmin", 0)

  console.log(`params--`, params.token)
  if (params.token) {
    localStorage.setItem("token", atob(params.token))
    window.location.href = `${HOST_URL}walls/home`
  }
  return <LoadingScreen />
}

// This component extracts login information from the URL and logs in with Shopify
const ShopifyLogin = ({history}) => {
  // Extract login information from the URL
  const location = useLocation();
  const code = new URLSearchParams(location.search).get("code");
  const hmac = new URLSearchParams(location.search).get("hmac");
  const host = new URLSearchParams(location.search).get("host");
  const shop = new URLSearchParams(location.search).get("shop");
  const timestamp = new URLSearchParams(location.search).get("timestamp");
   // Log in with Shopify
  useEffect(() => {
    loginWithShopify({code, hmac, host, shop, timestamp: parseInt(timestamp)},history);
  }, []);
  return <ShopifyLoadingScreen/>;
}

const ShopifyInstall = () => {
  const location = useLocation();
  const shop = new URLSearchParams(location.search).get("shop");
  useEffect(() => {
    installShopify(shop)
  }, [])
  return <Suspense fallback={null}><ShopifyLoadingScreen/></Suspense>
} 

const BigCommerceAuth = ({history}) => {

  const location = useLocation();
  const code = new URLSearchParams(location.search).get("code");
  const account_uuid = new URLSearchParams(location.search).get("account_uuid");
  const context = new URLSearchParams(location.search).get("context");
  const scope = new URLSearchParams(location.search).get("scope");

  useEffect(() => {
    loginWithBigcommerce({code, account_uuid, context, scope},history);
  }, []);
  
  return <LoadingScreen />
}

const BigCommercLogin = () => {
  const location = useLocation()
  const signed_payload = new URLSearchParams(location.search).get("signed_payload")
  const signed_payload_jwt = new URLSearchParams(location.search).get("signed_payload_jwt")
  useEffect(() => {
    BIGCOMMERCE_LOAD_LOGIN({signed_payload, signed_payload_jwt} )
  }, [])
  
  return <LoadingScreen />
}

export default function LoginBasePage(props) {
  const navigate = useNavigate()

  const location = useLocation();
  useEffect(() => {
    if (!localStorage.getItem("token") && !window.location.pathname.includes('/accounts') && !window.location.pathname.includes('/connection') && !window.location.pathname.includes('/bigcommerce')  && !window.location.pathname.includes('/Shopify') && !window.location.pathname.includes('/autologin') && !window.location.pathname.includes('/redirectlogin') && !window.location.pathname.includes('/ugc_right')&& !window.location.pathname.includes('/feeds/authCallback')) navigate('/')
  }, [])



  return (
    <ErrorFound>
     <PageHead />
      <Routes>
        <Route exact path="/autologin" element={<SwitchInTaggshop history={props.history} />} />
        <Route exact path="/redirectlogin/:token/:isAdmin" element={<Redirectlogin />} />
        <Route exact path="/redirectlogin/:token" element={<Redirectlogin />} />
        <Route exact path="/" element={<LoginLayout><Suspense fallback={null}><Login prefix="/" history={navigate} /></Suspense></LoginLayout>} />
        <Route exact path="/accounts/register" element={<LoginLayout><Suspense fallback={null}><SignUp prefix="/" history={navigate} /></Suspense></LoginLayout>} />
        <Route exact path="/accounts/login" element={<LoginLayout><Suspense fallback={null}><Login prefix="/" history={navigate} /></Suspense></LoginLayout>} />
        <Route exact path="/accounts/forgotpassword" element={<LoginLayout><Suspense fallback={null}><ForgotPassword prefix="/" activeProduct="0" /></Suspense></LoginLayout>} />
        <Route exact path="/accounts/resetpassword/:userId/:token" element={<LoginLayout><UpdateResetPassword prefix="/" /></LoginLayout>} />


        <Route exact path="/accounts/otherinfo" element={<LoginLayout><OtherInfo prefix="/" addEmail={false} history={props.history} /></LoginLayout>} />
        <Route exact path="/accounts/extemail" element={<LoginLayout><OtherInfo addEmail={true} prefix="/" history={props.history} /></LoginLayout>} />


        <Route exact path="/accounts/plan" element={<Suspense fallback={null}><NewUserPricePage history={props.history} /></Suspense>} />
        <Route exact path="/accounts/verifyemail/:id" element={<UpdateVerifyEmail prefix="/" history={props.history} />} />
        <Route exact path="/accounts/verify" element={<Suspense fallback={null}><AccountVerify prefix="/" history={props.history} /></Suspense>} />
        <Route exact path="/product/switch/display" element={<Suspense fallback={null}><GettingStarted /></Suspense>} />
        <Route path="*" element={<Navigate to={`/accounts/login`} />} />

        <Route exact path="/feeds/authLinkCallback/" element={<LinkedInCallback />}></Route>

        {/* <Route exact path="/feeds/authCallback" element={<UgcCallback />} />  */}
        <Route exact path="/feeds/authCallback" element={<Authorization />} /> 

        <Route exact path="/ugc_right/:id" element={<UgcScreen />} /> 
        <Route exact path="/ugc_right/authCallback" element={<UgcFallback />} />



        <Route exact path="/accounts/confirmaccount/:passScreen/:userId/:wallId" element={<CollabratorConfirmAccount />} />
        <Route exact path="/accounts/userdelete/:userId/:wallId" element={<RejectCollabratorRequest />} />
        <Route path="/Shopify/token" element={<ShopifyLogin  history={navigate} />} />
        <Route path="/Shopify/install" element={<ShopifyInstall />} />
        <Route path="/bigcommerce/oauth" element={<BigCommerceAuth history={navigate} />} />
        <Route path="/bigcommerce/load" element={<BigCommercLogin />} />
        {/* http://localhost:3000/v2/Shopify/install?shop=jewelsofjaipurindia.myshopify.com */}
        <Route exact path="/connection/:id" element={<ShareAccount />} />


      </Routes>

    </ErrorFound>
  )
}

